@import url("https://fonts.googleapis.com/css2?family=Changa:wght@200..800&display=swap");
.main {
  background: #f8f8f5;
}
.container {
  width: 80%;
  margin: 0 auto;
  padding: 120px 0px 80px 0px;
}
.container .articleTxt {
  margin-top: 15px;
  padding: 20px;
  background-color: #ffffff;
  box-shadow: 0 2px 5px rgba(0, 0, 0, 0.1);
  border-radius: 8px;
}
.container .articleTxt * {
  font-family: "Changa", serif;
}
.container .articleTxt h3 {
  font-size: 24px;
  margin-top: 5px;
}
.container .articleTxt ul {
  list-style: inside;
}
@media (max-width: 991.98px) {
  .container {
    width: 95%;
    margin: 0 auto;
    padding: 120px 10px 40px 10px;
  }
  .container .articleTxt h3 {
    font-size: 18px;
  }
}
