.main {
  background-image: url("./../../assets/bkg.jpg");
  background-size: cover;
  background-repeat: no-repeat;
  background-position: bottom;
}
.container {
  width: 80%;
  margin: 0 auto;
  padding: 120px 0px 80px 0px;
}
.container p {
  max-width: 1000px;
}
.me {
  margin: 20px 0;
  display: flex;
  align-items: center;
}
.me h2 {
  font-size: 48px;
  margin-left: 30px;
}
.container .img {
  width: 250px;
  height: 250px;
}
.container .img img {
  max-width: 100%;
  height: auto;
}
.btns {
  margin-top: 20px;
  display: flex;
  width: fit-content;
}
@media (max-width: 767.98px) {
  .container {
    width: 100%;
    padding: 120px 10px 40px 10px;
  }
}
/*Tablets*/
@media (max-width: 991.98px) {
  .me {
    flex-direction: column;
  }
  .container .img {
    width: 250px;
    height: 250px;
  }
  .me h2 {
    font-size: 30px;
    margin-left: 0px;
    text-align: left;
    align-self: flex-start;
  }
  .btns {
    margin: 20px auto;
  }
}
