.container {
  width: 80%;
  margin: 0 auto;
  padding: 120px 0px 80px 0px;
}
.container a {
  display: block;
  height: 40px;
  width: 120px;
  display: flex;
  justify-content: center;
  align-items: center;
  padding: 10px 20px;
  font-family: "Poppins", serif;
  font-size: 16px;
  border-radius: 8px;
  background-color: #2c3e50;
  color: #fff;
  transition: all 0.3s;
  margin: 0 auto;
}
.container a:hover {
  font-size: 18px;
}
.img {
  max-width: 700px;
  margin: 40px auto;
}
.img img {
  width: 100%;
  height: auto;
}
.body h2 {
  text-transform: capitalize;
  margin-bottom: 10px;
  font-size: 32px;
}
.body ul {
  list-style: inside;
}
.body ul li {
  font-family: "Merriweather", serif;
  font-size: 18px;
  line-height: 1.5;
  color: #34495e;
}
@media (max-width: 767.98px) {
  .container {
    width: 100%;
    padding: 120px 10px 40px 10px;
  }
  .body h2 {
    text-transform: capitalize;
    margin-bottom: 10px;
    font-size: 28px;
  }
}
