.main {
  background-image: url("./../../assets/bkg.jpg");
  background-size: cover;
  background-repeat: no-repeat;
  background-position: bottom;
}
.container {
  width: 80%;
  margin: 0 auto;
  padding: 120px 0px 80px 0px;
}
.projectsWrapper {
  display: flex;
  gap: 20px;
  flex-wrap: wrap;
  margin-top: 30px;
}
.projectholder {
  width: 300px;
  height: 320px;
  overflow: hidden;
  background-color: #ffffff;
  box-shadow: 0 2px 5px rgba(0, 0, 0, 0.1);
  border-radius: 8px;
}
.projectholder .img {
  width: 100%;
  height: 250px;
  overflow: hidden;
}
.projectholder .img img {
  max-width: 100%;
  height: 250px;
  transition: all 0.3s;
}
.projectholder .img img:hover {
  transform: scale(1.1);
}
.projectholder a > h2 {
  text-align: center;
  margin: auto;
  margin-top: 20px;
  transition: all 0.2s;
}
.projectholder a > h2:hover {
  color: #e74c3c;
}
@media (max-width: 767.98px) {
  .container {
    width: 100%;
    padding: 120px 10px 40px 10px;
  }
  .projectsWrapper {
    justify-content: center;
  }
}
