@import url("https://fonts.googleapis.com/css2?family=Changa:wght@200..800&display=swap");
.main {
  background-image: url("./../../assets/bkg.jpg");
  background-size: cover;
  background-repeat: no-repeat;
  background-position: bottom;
}
.container {
  width: 80%;
  margin: 0 auto;
  padding: 120px 0px 80px 0px;
}
.articlesWrapper {
  margin-top: 30px;
}
.articleholder {
  border-bottom: 3px solid #dfdfdf;
  padding: 20px 0;
  display: flex;
  gap: 30px;
  align-items: center;
}
.articleholder .img img {
  width: 300px;
}
.articleholder .info a {
  display: inline-block;
  width: fit-content;
}
.articleholder .info a h2 {
  transition: all 0.2s;
}
.articleholder .info a h2:hover {
  color: #e74c3c;
}
.articleholder .info h2 {
  font-size: 36px;
}
.articleholder .info p {
  width: 70%;
  font-family: "Changa", serif;
  /* direction: rtl; */
}
@media (max-width: 767.98px) {
  .articleholder {
    flex-direction: column;
    text-align: center;
  }
  .articleholder .img img {
    width: 250px;
  }
  .articleholder .info p {
    width: 100%;
    margin-top: 20px;
  }
}
@media (max-width: 991.98px) {
  .container {
    width: 100%;
    padding: 120px 10px 40px 10px;
  }
}
