@import url("https://fonts.googleapis.com/css2?family=Merriweather:ital,wght@0,300;0,400;0,700;0,900;1,300;1,400;1,700;1,900&family=Poppins:ital,wght@0,100;0,200;0,300;0,400;0,500;0,600;0,700;0,800;0,900;1,100;1,200;1,300;1,400;1,500;1,600;1,700;1,800;1,900&display=swap");
/*=======================================*/
/*General*/
* {
  padding: 0;
  margin: 0;
  box-sizing: border-box;
}
h1,
h2,
h3,
h4,
h5,
h6 {
  font-family: "Poppins", serif;
  line-height: 1.2;
  color: #2c3e50;
}
p,
a {
  font-family: "Merriweather", serif;
  font-size: 18px;
  line-height: 1.5;
  color: #34495e;
}
a {
  text-decoration: none;
}
.container {
  width: 90%;
  margin: 0 auto;
}
/* width */
::-webkit-scrollbar {
  width: 5px;
}
/* Handle */
::-webkit-scrollbar-thumb {
  background: #48bbd8;
  border: 1px solid #48bbd8;
}
/*=======================================*/
/*Navbar*/
.nav {
  /* background-color: #f4f6f7; */
  position: fixed;
  width: 100%;
  height: 80px;
  display: flex;
  align-items: center;
  background-color: #fff;
  box-shadow: rgba(0, 0, 0, 0.1) 0px 4px 6px -1px,
    rgba(0, 0, 0, 0.06) 0px 2px 4px -1px;
}
.container {
  width: 90%;
  margin: 0 auto;
}
.navWrapper {
  display: flex;
  justify-content: space-between;
  align-items: center;
}
.logo {
  max-width: 200px;
}
.logo > img {
  max-width: 100%;
  height: auto;
}
.links {
  list-style: none;
  display: flex;
}
.links li {
  margin-right: 20px;
}
.links li a {
  font-size: 20px;
  font-family: "Poppins", serif;
  transition: all 0.2s;
}
.links li a:hover {
  color: #e74c3c;
}
/*=====================================*/
/*Media Queries*/
/*Phones*/
@media (max-width: 767.98px) {
  p,
  a {
    font-size: 16px;
  }
  .container {
    width: 100%;
    padding-left: 10px;
    padding-right: 10px;
  }
  .logo {
    max-width: 100px;
  }
  .links li {
    margin-right: 10px;
  }
}
/*Tablets*/
@media (max-width: 991.98px) {
  .links li a {
    font-size: 16px;
  }
}
